#index-header {
  display: none;
}

#mobile-header {
  z-index: 10;
  border-bottom: solid 4px var(--main-color);
  width: 100%;
  display: block;
  position: sticky;
  top: 0;
}

#mobile-nav {
  background: linear-gradient(to right, var(--main-color), var(--third-color));
  width: 100%;
}

#mobile-nav-bar {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  height: 70px;
  padding: .5rem 1rem;
  display: flex;
}

#mobile-logo {
  align-items: center;
  height: 100%;
  display: flex;
}

#mobile-logo img {
  border: 2px solid #fff;
  width: 130px;
  height: 100%;
  transition: all .1s;
}

#mobile-logo img:hover {
  border-color: var(--secondary-color);
}

#mobile-menu-btn {
  color: #fff;
  cursor: pointer;
  background: none;
  border: none;
  font-size: 2rem;
}

#mobile-menu {
  background-color: var(--third-color);
  z-index: 3;
  box-sizing: border-box;
  border: solid var(--fourth-color) 2px;
  width: 99%;
  height: 100vh;
  margin-left: .5%;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

#mobile-menu-close {
  color: #fff;
  box-sizing: border-box;
  cursor: pointer;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  height: 7vh;
  margin-bottom: 0;
  padding-right: 3%;
  font-size: 30px;
  display: flex;
}

#mobile-menu-links {
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 63%;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
  display: flex;
}

.mobile-menu-links-li {
  border-bottom: solid var(--fourth-color) 1.5px;
  cursor: pointer;
  width: 100%;
  height: 12.5%;
}

.mobile-menu-links-li-a {
  color: var(--fourth-color);
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  text-decoration: none;
  display: flex;
}

#mobile-menu-links-selected {
  background-color: var(--fourth-color);
}

#mobile-menu-links-selected a {
  color: var(--third-color);
}

#mobile-menu-info {
  box-sizing: border-box;
  background-color: #000;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  height: 30%;
  margin-top: 0;
  display: flex;
}

.mobile-menu-info {
  box-sizing: border-box;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 22%;
  display: flex;
}

.mobile-menu-info-div1 {
  box-sizing: border-box;
  justify-content: flex-start;
  align-items: center;
  min-width: 30%;
  height: 100%;
  padding-left: 2%;
  display: flex;
}

.mobile-menu-info-div1 h4 {
  color: #fff;
  margin-left: 4%;
}

.mobile-menu-info-anchor {
  color: #fff;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 100%;
  padding-left: 1%;
  text-decoration: none;
  display: flex;
  overflow-x: scroll;
}

#mobile-menu-info-social {
  box-sizing: border-box;
  border: 2px solid #fff;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 30%;
  margin-top: 0;
  padding-left: 0;
  list-style: none;
  display: flex;
}

.mobile-menu-info-social-li {
  height: 100%;
  color: var(--secondary-color);
  margin-left: 1%;
}

.mobile-menu-info-social-li2 {
  height: 100%;
  margin-left: 3%;
}

.mobile-menu-info-social-li2-a {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
}

.mobile-menu-info-social-li2-img {
  width: 20px;
  height: 20px;
}

#news_and_events_main {
  display: none;
}

#news_and_events_main_mobile {
  box-sizing: border-box;
  flex-direction: column;
  align-items: center;
  padding: 4%;
  display: flex;
}

.mobile-page-title {
  text-align: center;
  background-color: var(--fourth-color);
  width: 100%;
  color: var(--third-color);
  margin-bottom: 4%;
  padding: 4%;
  text-decoration: underline;
}

.mobile-toggle {
  width: 100%;
  margin-bottom: 2%;
  display: flex;
}

.mobile-toggle-btn {
  background-color: var(--fourth-color);
  width: 50%;
  color: var(--third-color);
  cursor: pointer;
  border: none;
  padding: 4%;
  font-size: 105%;
}

.mobile-toggle-btn.active {
  background-color: var(--third-color);
  color: var(--fourth-color);
}

#nav_sort_by {
  box-sizing: border-box;
  background-color: var(--third-color);
  width: 100%;
  color: var(--fourth-color);
  justify-content: space-around;
  align-items: center;
  margin-bottom: 4%;
  padding: 1%;
  display: flex;
}

#news_and_events_select, #news_select, #events_select {
  background-color: var(--fourth-color);
  color: var(--third-color);
  cursor: pointer;
  border: solid 1px var(--fourth-color);
  justify-content: center;
  align-items: center;
  padding: 1%;
  display: flex;
}

#mobile-card-news, #mobile-card-events {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
}

.mobile-no {
  background-color: var(--third-color);
  color: var(--fourth-color);
  padding: 2%;
}

.mobile-card {
  border: solid var(--third-color) 2px;
  box-sizing: border-box;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  min-width: 100%;
  height: 50vh;
  margin-bottom: 5%;
  padding: 1.5%;
  display: flex;
}

.mobile-card-div1 {
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 20%;
  margin-bottom: 0;
  display: flex;
}

.mobile-date {
  background-color: var(--fourth-color);
  color: var(--third-color);
  padding: 2%;
  font-size: 90%;
}

.mobile-category {
  background-color: var(--main-color);
  color: var(--fourth-color);
  padding: 2%;
  font-size: 90%;
  overflow-x: scroll;
}

.mobile-title {
  background-color: var(--third-color);
  color: var(--fourth-color);
  width: 100%;
  height: 14%;
  margin-top: 1%;
  padding: 2% 2% 2% 4%;
  overflow: scroll;
}

.mobile-description {
  color: var(--third-color);
  box-sizing: border-box;
  width: 100%;
  height: 40%;
  margin-top: 0;
  padding: 1%;
  font-size: 16px;
  overflow-y: scroll;
}

.mobile-description::-webkit-scrollbar {
  width: 5px;
}

.mobile-description::-webkit-scrollbar-thumb {
  background: var(--third-color);
}

.mobile-description::-webkit-scrollbar-track {
  background: var(--fourth-color);
}

.mobile-time {
  background-color: var(--main-color);
  color: var(--fourth-color);
  justify-content: center;
  align-items: center;
  padding: 2%;
  font-size: 90%;
  display: flex;
}

.mobile-card-div2 {
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 20%;
  display: flex;
}

.mobile-address {
  box-sizing: border-box;
  background-color: var(--fourth-color);
  max-width: 70%;
  height: 100%;
  color: var(--third-color);
  padding: 1%;
  overflow-y: scroll;
}

.mobile-address::-webkit-scrollbar {
  width: 5px;
}

.mobile-address::-webkit-scrollbar-thumb {
  background: var(--third-color);
}

.mobile-address::-webkit-scrollbar-track {
  background: var(--fourth-color);
}

#index-footer {
  display: none;
}

#index-footer-mobile {
  color: #fff;
  box-sizing: border-box;
  background-color: #000;
