  width: 100%;
  margin-bottom: 0;
  padding: 2%;
  display: flex;
}

#index-footer-mobile-div1 {
  text-align: center;
}

#index-footer-mobile-p1 {
  margin: 0;
  font-size: 13px;
  line-height: 1.4;
}

#index-footer-mobile-div2 {
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
}

#index-footer-mobile-p2 {
  margin: 0;
  font-size: 13px;
}

#index-footer-mobile-a1 {
  color: #fff;
  border-radius: 6px;
  padding: 6px 10px;
  font-size: 13px;
  text-decoration: underline #fff;
  transition: all .3s;
}

#index-footer-mobile-a1:hover {
  background-color: var(--third-color);
  color: var(--fourth-color);
}

#index-footer-mobile-div3 {
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
}

#index-footer-mobile-p2 {
  color: var(--fourth-color);
  justify-content: space-around;
  align-items: center;
  text-decoration: none;
  display: flex;
}

#index-footer-mobile-p2:hover {
  color: #fff;
}
