/* MAIN HEADER */
  #index-header {
    display: none;
  }

  #mobile-header {
    position: sticky;
    top: 0;
    z-index: 10;
    width: 100%;
    border-bottom: solid 4px var(--main-color);
    display: block;

}

#mobile-nav {
  background: linear-gradient(
    to right,
    var(--main-color),
    var(--third-color)
  );
  width: 100%;
}

#mobile-nav-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1rem;
  height: 70px;
  box-sizing: border-box;
}

#mobile-logo {
  height: 100%;
  display: flex;
  align-items: center;

}

#mobile-logo img {
  height: 100%;
  border: solid white 2px;
  transition: 0.1s;
  width: 130px;
}

#mobile-logo img:hover {
  border-color: var(--secondary-color);
}

#mobile-menu-btn {
  font-size: 2rem;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
}

#mobile-menu {
  background-color: var(--third-color);
  height: 100vh;
  width: 99%;
  z-index: 3;
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  border: solid var(--fourth-color) 2px;
  margin-left: 0.5%;
  overflow: hidden;
display: none;
}

#mobile-menu-close{
  width: 100%;
  font-size: 30px;
  color: white;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-right: 3%;
  box-sizing: border-box;
  cursor: pointer;
  height: 7vh;
  margin-bottom: 0;

}

#mobile-menu-links{

  margin-top: 0;
  list-style: none;
  width: 100%;
  height: 63%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding-left: 0;
  margin-bottom: 0;
}

.mobile-menu-links-li {
  border-bottom: solid var(--fourth-color) 1.5px;
  width: 100%;
  cursor: pointer;
  height: 12.5%;
}

.mobile-menu-links-li-a{
  text-decoration: none;
  color: var(--fourth-color);
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

#mobile-menu-links-selected{
  background-color: var(--fourth-color);
}
#mobile-menu-links-selected a {
  color: var(--third-color);
}

#mobile-menu-info{
  margin-top: 0;
  background-color: black;
  width: 100%;
  height: 30%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.mobile-menu-info{
  width: 100%;
  height: 22%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  box-sizing: border-box;
}

.mobile-menu-info-div1{
  height: 100%;
  min-width: 30%;

  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 2%;
  box-sizing: border-box;
}

.mobile-menu-info-div1 h4 {
  color: white;
  margin-left: 4%;
}

.mobile-menu-info-anchor{

  height: 100%;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 1%;
  overflow-x: scroll;
  text-decoration: none;
  color: white;

}

#mobile-menu-info-social{
  border: solid white 2px;
  width: 100%;
  height: 30%;
  box-sizing: border-box;
  margin-top: 0;
  display: flex ;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-left: 0;
  list-style: none;
}

.mobile-menu-info-social-li{
  height: 100%;
  color: var(--secondary-color);
  margin-left: 1%;
}


.mobile-menu-info-social-li2{
  height: 100%;
  margin-left: 3%;
}

.mobile-menu-info-social-li2-a{
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.mobile-menu-info-social-li2-img{
  height:  20px;
  width: 20px;
}

/* news and events mobile */
#news_and_events_main{
  display: none;
}

/* ===============================
   MOBILE NEWS & EVENTS
================================ */

#news_and_events_main{
  display: none;
}

#news_and_events_main_mobile {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 4%;
  box-sizing: border-box;
}

/* Page title */
.mobile-page-title {
  width: 100%;
  padding: 4%;
  text-align: center;
  background-color: var(--fourth-color);
  color: var(--third-color);
  text-decoration: underline;
  margin-bottom: 4%;
}

/* Toggle buttons */
.mobile-toggle {
  width: 100%;
  display: flex;
  margin-bottom: 2%;
}

.mobile-toggle-btn {
  width: 50%;
  padding: 4%;
  border: none;
  background-color: var(--fourth-color);
  color: var(--third-color);
  font-size: 105%;
  cursor: pointer;
}

.mobile-toggle-btn.active {
  background-color: var(--third-color);
  color: var(--fourth-color);
}

#nav_sort_by{

  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 4%;
  padding: 1%;
  box-sizing: border-box;
  background-color: var(--third-color);
  color: var(--fourth-color);
}

#news_and_events_select{
  background-color: var(--fourth-color);
  color: var(--third-color);
  cursor: pointer;
  border: solid 1px var(--fourth-color);
  padding: 1%;
  display: flex;
  justify-content: center;
  align-items: center;
}

#news_select{
  background-color: var(--fourth-color);
  color: var(--third-color);
  cursor: pointer;
  border: solid 1px var(--fourth-color);
  padding: 1%;
  display: flex;
  justify-content: center;
  align-items: center;
}

#events_select{
  background-color: var(--fourth-color);
  color: var(--third-color);
  cursor: pointer;
  border: solid 1px var(--fourth-color);
  padding: 1%;
  display: flex;
  justify-content: center;
  align-items: center;
}

#mobile-card-news{
  
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  width: 100%;
}

#mobile-card-events{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%
}


.mobile-no{
  background-color: var(--third-color);
  padding: 2%;
  color: var(--fourth-color);
}

.mobile-card {
  min-width: 100%;
  border: solid var(--third-color) 2px;
  padding: 1.5%;
  margin-bottom: 5%;
  box-sizing: border-box;
  height: 50vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;

}

.mobile-card-div1{
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 20%;
  margin-bottom: 0;

}

.mobile-date {
  font-size: 90%;
  background-color: var(--fourth-color);
  color: var(--third-color);
  padding: 2%;
}

.mobile-category {
  background-color: var(--main-color);
  color: var(--fourth-color);
  padding: 2%;
  font-size: 90%;
  overflow-x: scroll;
}

.mobile-title {
  background-color: var(--third-color);
  color: var(--fourth-color);
  padding: 2%;
  width: 100%;
  padding-left: 4%;
  margin-top: 1%;
  height: 14%;
  overflow: scroll;
}

.mobile-description {
  font-size: 16px;
  color: var(--third-color);
  margin-top: 0;
  overflow-y: scroll;
  height: 40%;
  width: 100%;
  padding: 1%;
  box-sizing: border-box;
}
.mobile-description::-webkit-scrollbar {
  width: 5px;
}
/* scroll bar thumb (the actual scroll) */
.mobile-description::-webkit-scrollbar-thumb {
  background: var(--third-color); /* scrollbar color */
}
/* scrroll bar track */
.mobile-description::-webkit-scrollbar-track {
  background: var(--fourth-color); /* track color */
}

.mobile-time{
  background-color: var(--main-color);
  color: var(--fourth-color);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2%;
  font-size: 90%;
}

.mobile-card-div2{
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 20%;
}

.mobile-address{

  padding: 1%;
  max-width: 70%;
  height:  100%;
  box-sizing: border-box;
  background-color: var(--fourth-color);
  color: var(--third-color);
  overflow-y: scroll;
}
.mobile-address::-webkit-scrollbar {
  width: 5px;
}
/* scroll bar thumb (the actual scroll) */
.mobile-address::-webkit-scrollbar-thumb {
  background: var(--third-color); /* scrollbar color */
}
/* scrroll bar track */
.mobile-address::-webkit-scrollbar-track {
  background: var(--fourth-color); /* track color */
}

/*   FOOTER */
#index-footer{
  display: none;
}
#index-footer-mobile {
  width: 100%;
  background-color: black;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  margin-bottom: 0px;
    padding: 2%;
}

#index-footer-mobile-div1 {
  text-align: center;

}

#index-footer-mobile-p1 {
  font-size: 13px;
  line-height: 1.4;
  margin: 0;
  
}


#index-footer-mobile-div2 {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;

}

#index-footer-mobile-p2 {
  font-size: 13px;
  margin: 0;
}

#index-footer-mobile-a1 {
  font-size: 13px;
  color: white;
  text-decoration: underline;
  text-decoration-color: white;
  padding: 6px 10px;
  border-radius: 6px;
  transition: 0.3s;
}

#index-footer-mobile-a1:hover {
  background-color: var(--third-color);
  color: var(--fourth-color);
}

/* ADMIN */
#index-footer-mobile-div3 {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

}

#index-footer-mobile-p2{
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-decoration: none;
  color: var(--fourth-color);
}
#index-footer-mobile-p2:hover{
  color: white;
}














